.button {
	border-radius: 4px;
	height: 50px;
	padding: 0 15px;
	display: inline-flex;
	justify-content: space-between;
	align-items: center;
	font-weight: 700;

	&.orange {
		background-image: linear-gradient(-180deg, #FB5101 90%, #FF7022 100%);
		box-shadow: 0 2px 2px 0 rgba(0,0,0,0.10);
		color: white;
	}
}
